/**
 * @typedef {string} EColor
 */
/**
 *
 * @enum {EColor}
 */
const E_COLORS = Object.freeze({
    RESET: '\x1b[0m',
    WHITE: '\x1b[37m',
    BLUE: '\x1b[34m',
    YELLOW: '\x1b[33m',
    RED: '\x1b[31m',
    GREEN: '\x1b[32m',
    DAK_GREY: '\x1b[30m',
});

export default E_COLORS;